import React, { Component, Fragment } from 'react';
import { CustomInput, Input, Label, FormGroup, Row, Col, Button, Badge } from 'reactstrap';
import { JOFGENDesigner, isValidDesignJSON } from '../../lib';
import '../../lib/css/designer.css';
import Icon from '@mdi/react';
import { local_urls } from '../../urls';

export default class Playground_Designer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            export: false,
            extended: false,
            fatal_error_msg: "Ooops... Something went wrong. Unable to render designer.",
            json: "",
            jsonObj: [],
            mode: "linear",
            size: "sm",
            title: "Designer",
            is_valid: true,
            translation: ""
        };
    }

    onChange = function (e) {
        if (e.target.name === 'json')
        {
            var jsonObj;
            var isValid;
            try
            {
                jsonObj = JSON.parse(e.target.value);
                isValid = isValidDesignJSON(jsonObj, this.state.mode);
            }
            catch
            {
                jsonObj = e.target.value;
                isValid = false;
            }

            this.setState({
                json: e.target.value,
                jsonObj: jsonObj,
                is_valid: isValid
            });
        }
        else
        {
            var obj = {};
            obj[e.target.name] = e.target.value;
            this.setState(obj);
        }
    }.bind(this)

    onChangeDesigner = function (json, valid) {
        this.setState({
            jsonObj: json,
            is_valid: valid,
            json: JSON.stringify(json)
        })
    }.bind(this)

    onClear = function()
    {
        this.setState({
            export: false,
            extended: false,
            fatal_error_msg: "Ooops... Something went wrong. Unable to render designer.",
            jsonObj: [],
            json: "",
            mode: "linear",
            size: "sm",
            translation: "",
            title: "Designer",
            is_valid: true
        });
    }.bind(this)
    
    onChangeBool = function (e) {
        var state = {};
        state[e.target.name] = e.target.checked;
        this.setState(state);
    }.bind(this)

    render() {
        return (
            <Fragment>
                <section>
                    <div className="mt-3">
                        <Row>
                            <Col className="mx-300">
                                <div className="p-4 card filter">
                                    <div className="title">Properties</div>
                                    <FormGroup className="pt-1" check>
                                        <Label check>
                                            <Input name="export" type="checkbox" onChange={this.onChangeBool} checked={this.state.export} />{' '}
                                            export
                                        </Label>
                                    </FormGroup>
                                    <FormGroup check>
                                        <Label check>
                                            <Input name="extended" type="checkbox" onChange={this.onChangeBool} checked={this.state.extended} />{' '}
                                            extended
                                        </Label>
                                    </FormGroup>
                                    <FormGroup check>
                                        <Label check>
                                            <Input name="hideDissabled" type="checkbox" onChange={this.onChangeBool} checked={this.state.hideDissabled} />{' '}
                                            hideDissabled
                                        </Label>
                                    </FormGroup>
                                    <FormGroup className="pt-2">
                                        <Label>fatal_error_msg</Label>
                                        <Input type="text" bsSize="sm" name="fatal_error_msg" value={this.state.fatal_error_msg} onChange={this.onChange} />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label>json</Label>
                                        <div style={{ float:'right' }}>
                                            {(this.state.is_valid || this.state.json.length === 0)?<Badge color="success">Valid</Badge>:<Badge color="danger">Invalid</Badge>}
                                        </div>
                                        <Input type="textarea" bsSize="sm" name="json" value={this.state.json} onChange={this.onChange} />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label>mode</Label>
                                        <CustomInput type="select" bsSize="sm" name="mode" value={this.state.mode} onChange={this.onChange}>
                                            <option value="linear">linear</option>
                                            <option value="linear_merge">linear_merge</option>
                                            <option value="tree">tree</option>
                                        </CustomInput>
                                    </FormGroup>
                                    <FormGroup>
                                        <Label>size</Label>
                                        <CustomInput type="select" bsSize="sm" name="size" value={this.state.size} onChange={this.onChange}>
                                            <option value="sm">small</option>
                                            <option value="lg">large</option>
                                        </CustomInput>
                                    </FormGroup>
                                    <FormGroup className="pt-2">
                                        <Label>title</Label>
                                        <Input type="text" bsSize="sm" name="title" value={this.state.title} onChange={this.onChange} />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label>translation</Label>
                                        <Input type="textarea" bsSize="sm" name="translation" value={this.state.translation} onChange={this.onChange} />
                                    </FormGroup>                               
                                    <div className="mt-3 btns text-right">
                                        <Button color="link" size="sm" onClick={this.onClear} >Clear</Button>
                                    </div>
                                </div>
                            </Col>
                                <Col>
                                    <div className="p-2">
                                        <JOFGENDesigner 
                                            export={this.state.export}
                                            extended={this.state.extended}
                                            title={(this.state.title)?this.state.title:undefined} 
                                            translation={(this.state.translation)?JSON.parse(this.state.translation):undefined} 
                                            size={this.state.size}                                            
                                            mode={this.state.mode} 
                                            fatal_error_msg={(this.state.fatal_error_msg)?this.state.fatal_error_msg:undefined}
                                            json={(this.state.json)?this.state.jsonObj:[]} 
                                            hideDissabled={this.state.hideDissabled}
                                            onChange={this.onChangeDesigner} />
                                    </div>
                                </Col>
                        </Row>
                    </div>
                </section>
            </Fragment>
        );

    }
}